.about {
    background: #191919;
}

.about__main {
    background: #191919;
}

.about__main h1 {
    padding-top: 60px;
    display: flex;
    position: relative;
    justify-content: center;
    font-size: 60px;
    font-weight: 400;
    padding-bottom: 40px;
}

.about__main h1 span {
    font-size: 60px;
    font-weight: 500;
    -webkit-text-stroke: 2px #fff;
    color: black;
}

.about__content h3 {
    font-size: 40px;
    font-weight: 400;
}

.about__isi {
    color: #fff;
    font-weight: 300;
    text-align: justify;
    font-size: 20px;
}

img.about__img {
    border-radius: 10px;
    width: 100%;
}

h1.about__heading {
    margin-bottom: 10px;
    font-weight: 700;
    text-align: start;
    font-family: Montserrat, sans-serif;
    font-size: 60px;
}

button.about.btn.pointer {
    text-transform: capitalize;
    width: 195px;
    height: 55px;
    font-size: 18px;
    transition: .4s ease;
}

.about__button {
    padding: 20px 0px;
}

a.bottom__to__top {
    height: 40px;
    width: 40px;
    position: fixed;
    bottom: 3%;
    right: 2%;
    background-color: #F9004D;
    border-radius: 50%;
    cursor: pointer;
    display: none;
    align-items: center;
    justify-content: center;
    transition: .4s all ease;
    z-index: 1000;
}

a.bottom__to__top.active {
    display: flex;
}

/* RESPONSIVE */
@media only screen and (max-width: 1024px) {
    .p__color {
        font-size: 15px !important;
    }
}

@media only screen and (max-width: 425px) {
    button.about.btn.pointer {
        text-transform: capitalize;
        width: 165px;
        height: 50px;
        font-size: 13px;
        transition: .4s ease;
    }

    .about__h1,
    .about__span {
        font-size: 40px;
    }
}

.about__icon i {
    color: #fff;
}

.about__img {
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 5px;
}

.about__img img {
    width: 100% !important;
    border-radius: 5px;
}

.about__img__box {
    width: 200px !important;
}

@media only screen and (max-width: 992px) {
    .about__img__gallery {
        justify-content: center !important;
        margin-top: 50px;
    }

    .about__content h3 {
        font-size: 1.3rem;
    }
}

@media only screen and (max-width: 768px) {
    .about__img__box {
        width: 150px !important;
    }
}