@import url(http://fonts.cdnfonts.com/css/gotham);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  font-family: 'Gotham', sans-serif;
  scroll-behavior: smooth;
  text-decoration: none;
}

body {
  width: 100%;
  height: 100%;
  background-color: #000;
  font-family: 'Gotham', sans-serif;
}

a {
  text-decoration: none;
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6,
span {
  font-size: 18px;
  color: #fff;
  font-family: 'Gotham', sans-serif;
}

li {
  overflow: hidden;
}

span {
  display: block;
}

p {
  font-size: 16px;
  font-family: 'Gotham', sans-serif;
}

.white {
  color: #fff !important;
}

.black {
  color: #000 !important;
}

.d__flex {
  display: flex;
}

.align__items__center {
  align-items: center;
}

.justify__content__center {
  justify-content: center;
}

.justify__content__space__between {
  justify-content: space-between;
}

.mx__15 {
  margin: 0px 15px;
}

.px__30 {
  padding: 0px 30px;
}

.pxy__30 {
  padding: 30px;
}

.container {
  width: 92%;
  margin: 0px auto;
}

.pz__10 {
  padding: 10px 0px;
}

.sweet {
  color: #F9004D;
}

.row {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  overflow: hidden;
  align-items: flex-start;
  justify-content: center;
}

.col__2 {
  max-width: 50%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
}

.col__3 {
  max-width: 33.333333%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
  margin-top: 20px;
}

.component__space {
  padding: 100px 0px;
}

.pointer {
  cursor: pointer;
}

svg {
  stroke-width: 1 !important;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

strong {
  color: #F9004D;
  font-weight: 600;
}

input {
  color: rgba(198, 201, 216, .75);
}

button.btn {
  font-size: 18px;
  font-weight: 500;
  border-radius: 5px;
  border: 2px solid #f9004d;
  background-color: #f9004d;
  color: #fff !important;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: .4s all ease;
  height: 50px;
  width: 180px;
  margin: 0px 10px;
}

.p__color {
  color: rgba(198, 201, 216, .75);
  font-size: 18px;
  line-height: 30px;
  font-family: 'Gotham', sans-serif;
  margin-bottom: 15px;
}

button.about.btn.pointer:hover {
  background: #fff;
  color: #000 !important;
}

.footer__button {
  border: none;
  position: relative;
}

