}
.header {
    width: 100%;
    height: 80px;
    line-height: 80px;
}

.nav__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo h1 {
    position: relative;
    font-size: 1.5rem;
    padding-left: 20px;
    font-weight: 500;
    left: 50px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.nav__menu {
    display: flex;
    align-items: center;
    grid-column-gap: 2.8rem;
    -webkit-column-gap: 2.8rem;
            column-gap: 2.8rem;
    position: relative;
    right: 70px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.nav__menu a {
    color: #808dad;
    font-weight: 500;
    font-size: 1rem;
    text-decoration: none;
    transition: 0.3s;
    cursor: pointer;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.nav__menu a:hover {
    color: #fff;
}

.nav__menu a:first-child {
    color: #fff;
}

.nav__right p {
    color: #fff;
    margin-left: 20px;
    position: relative;
}

.nav__right i {
    color: #808dad;
}

.nav__right p::before {
    content: "";
    position: absolute;
    top: 25%;
    left: -20px;
    width: 1px;
    height: 14px;
    background: #808dad;
    color: #808dad;
}

/* .header__men {
    color: #fff;
} */

.header__shrink {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    line-height: 80px;
    background: rgba(0, 0, 0, 0.5);
    z-index: 9999;
    border-bottom: 1px solid rgba(0, 0, 0, 0.7);
}

.mobile__menu i {
    font-size: 1.3rem;
    color: #fff;
    padding-right: 20px;
    cursor: pointer;
}

.mobile__menu {
    display: none;
}

/* ========== make it responsive ========= */

@media only screen and (max-width: 992px) {
    .navigation {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #171f3897;
        z-index: 99999;
        display: none;
    }

    .nav__menu {
        position: absolute;
        top: 0;
        right: 0;
        width: 300px;
        height: 100%;
        flex-direction: column;
        justify-content: center;
    }

    .nav__right {
        display: none;
    }

    .mobile__menu {
        display: block !important;
    }

    .menu__active {
        display: block !important;
    }

    .logo h1 {
        font-size: 1.3rem !important;
    }
}

@media only screen and (max-width: 768px) {
    .header {
        height: 60px;
        line-height: 60px;
    }
}
